.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2{
  margin: 10px 0 0 0;
  text-align: center;
  color: gray;
}

h5{
  text-align: center;
  color: darkslategray;
  margin: -10px 0 0 0;
}

#searchIcon {
/* outline: solid; */
outline-width: thin;
border-color: white;
margin: 0 3px -5px 0;
}
